<template>
  <div class="hello">
    <p style="margin: auto">
      1. 输入自己的 ETH地址<br/>
      2. 进入 TG 群，输入/thanks 别人的邀请码+自己的邀请码<br/>
      3. 分享 xxx.io/自己的邀请码  到微信群，获取糖果加成<br/>
    </p>
    <input v-model="address" />
    <div><button @click="generateAddress()">生成eth地址</button></div>
  </div>
</template>

<script>
import md5 from 'md5';
export default {
  name: 'page',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      address: localStorage.getItem("address")
    }
  },
  methods: {
    generateAddress () {
      localStorage.setItem("address", md5(this.address))
      this.$router.push({path: '/eth', query: this.address})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



