<template>
  <div class="hello">
    <p>
      您的eth地址: {{address}}
    </p>
  </div>
</template>
<script>
export default {
  name: 'eth_address',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      address: localStorage.getItem("address")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



