import Vue from 'vue'
import Router from 'vue-router'
import Page from '@/components/main'
import EthAddress from '@/components/eth_address'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'page',
      component: Page
    },
    {
      path: '/eth',
      name: 'eth_address',
      component: EthAddress
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (!localStorage.getItem('address') && to.name !== 'main') {
    next({name: 'main'})
  }
  if (localStorage.getItem('address') && to.name !== 'eth_address') {
    next({name: 'eth_address'})
  }
  next()
});


export default router;


